<template>
	<div class="row">
		<div class="col-lg-12">
			<div class="card">
				<div class="card-header">
					<h4>Data kasus COVID-19 di Bekasi</h4>
				</div>
				<div class="card-body">
					<div class="table-responsive">
						<table class="table table-bordered table-md text-left">
							<thead>
								<tr>
									<th>KECAMATAN</th>
									<th>ODP</th>
									<th>PDP</th>
									<th>TOTAL</th>
									<th>TERINFEKSI</th>
								</tr>
							</thead>
							<tbody>
								<tr v-for="(result,index) in data" :key="index">
									<td>{{result.kecamatan}}</td>
									<td>{{result.odp}}</td>
									<td>{{result.pdp}}</td>
									<td>{{result.odp+result.pdp}}</td>
									<td>{{result.terkonfirmasi}}</td>
								</tr>
							</tbody>
						</table>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import { IndonesiaService } from "../../services/IndonesiaService";
	const apiService = new IndonesiaService();
	export default {
		name: "TableBekasi",
		data() {
			return {
				data: []
			};
		},
		methods: {
			getData() {
				apiService.getBekasiData().then(response => {
					this.data = response.Data;
				});
			}
		},
		mounted() {
			this.getData();
		}
	};
</script>